<template lang="pug">
  #membership.bg-white
    b-container.pt-5(fluid)
      b-table(
        show-empty
        empty-text=""
        striped
        stacked='md'
        :items='users'
        :fields='fields'
        thead-tr-class="bg-info")
        template(v-slot:cell(address)="row")
          | {{ (row.item.address.length === 0) ? '' : row.item.address }}
        template(v-slot:cell(action)="row")
          b-button(variant="danger" @click="cancel([row.item._id,row.index])") 
</template>

<script>
export default {
  data () {
    return {
      users: [],
      fields: [
        { key: 'name', label: '', class: 'text-center align-middle flexItem' },
        { key: 'tel', label: '', class: 'text-center align-middle' },
        { key: 'account', label: '', class: 'text-center align-middle' },
        { key: 'address', label: '', class: 'text-center align-middle' },
        { key: 'action', label: '', class: 'text-center align-middle' }
      ]
    }
  },
  mounted () {
    this.axios.get(process.env.VUE_APP_APIURL + '/membership')
      .then(response => {
        this.users = response.data.resultNew
      })
      .catch(error => {
        this.$vs.notify({
          title: error.response.data.message,
          color: 'danger',
          icon: 'close'
        })
      })
  },
  methods: {
    cancel (data) {
      this.$swal({
        title: '',
        icon: 'question',
        confirmButtonColor: '#3085d6',
        confirmButtonText: '',
        showCancelButton: true,
        cancelButtonColor: '#d33',
        cancelButtonText: ''
      }).then(result => {
        if (result.value) {
          this.axios.delete(process.env.VUE_APP_APIURL + '/membership/' + data[0])
            .then(response => {
              if (response.data.success) {
                this.$swal({
                  title: '',
                  icon: 'success',
                  confirmButtonColor: '#3085d6',
                  confirmButtonText: ''
                })
                this.users.splice(data[1], 1)
              }
            })
            .catch(e => {
              this.$swal({
                title: e.response.data.message,
                icon: 'danger',
                confirmButtonColor: '#3085d6',
                confirmButtonText: ''
              })
            })
        }
      })
    }
  }
}
</script>
