<template lang="pug">
  #privacy.bg-white
    b-container.h-100
      b-row.align-items-center.align-items-lg-end.personal
        b-col.text-center
          h3 
          h6 
      b-row.h-50
        b-col
          b-form.h-100.d-flex.flex-column.justify-content-center.align-items-center(@submit="submit")
            b-form-group.w-50(
              v-for="(item,index) in inputs"
              :key="index"
              :label="item.label"
              :label-for="`input-`+item.id"
            )
              b-form-input(
                required
                :id="`input-`+item.id"
                type="password"
                :state="item.state"
                v-model="item.model"
              )
              b-form-invalid-feedback(:state='item.state')
                | {{ item.warning }}
            b-btn(type='submit' variant="success" :disabled="disabled") 
</template>
<script>
export default {
  data () {
    return {
      inputs: [
        { label: '', id: '1', model: '' },
        { label: '', id: '2', model: '', state: null, warning: ' 6 ' },
        { label: '', id: '3', model: '', state: null, warning: '' }
      ]
    }
  },
  methods: {
    submit (e) {
      e.preventDefault()
      this.axios.patch(process.env.VUE_APP_APIURL + '/privacy/' + this.userID, this.form)
        .then(response => {
          if (response.data.success) {
            for (const i of this.inputs) {
              i.model = ''
            }
            this.$swal({
              title: '',
              icon: 'success',
              confirmButtonColor: '#3085d6',
              confirmButtonText: ''
            })
          }
        }).catch(error => {
          for (const i of this.inputs) {
            i.model = ''
          }
          this.$swal({
            title: error.response.data.message,
            icon: 'success',
            confirmButtonColor: '#3085d6',
            confirmButtonText: ''
          })
        })
    }
  },
  computed: {
    disabled () {
      return (this.form.password !== this.form.newconfirm || this.form.password.length < 6)
    },
    form () {
      return {
        old: this.inputs[0].model,
        password: this.inputs[1].model,
        newconfirm: this.inputs[2].model
      }
    },
    userID () {
      return this.$store.getters.userID
    }
  },
  watch: {
    form: {
      deep: true,
      handler (value) {
        this.inputs[1].state = (value.password.length === 0) ? null : (value.password.length >= 6)
        this.inputs[2].state = (value.password.length === 0 && value.newconfirm.length === 0) ? null : (value.password === value.newconfirm)
      }
    }
  }
}
</script>
