<template lang="pug">
  #message.bg-white
    b-container.pt-5(fluid)
      b-table(
        show-empty
        striped
        empty-text=""
        :items='items'
        :fields='fields'
        stacked='sm'
        thead-tr-class="bg-info")
        template(v-slot:cell(text)="row")
          b-button(size="sm" @click="row.toggleDetails" variant="info").
            {{ row.detailsShowing ? '' : '' }}
        template(v-slot:row-details="row")
          b-card.
            {{ row.item.text }}
</template>

<script>
export default {
  data () {
    return {
      items: [],
      fields: [
        { key: 'name', label: '', sortable: false, class: 'text-center align-middle' },
        { key: 'tel', label: '', sortable: false, class: 'text-center align-middle' },
        { key: 'email', label: 'E-Mail', sortable: false, class: 'text-center align-middle' },
        { key: 'text', label: '', sortable: false, class: 'text-center align-middle' },
        { key: 'content', label: '', sortable: true, class: 'text-center align-middle' }
      ]
    }
  },
  mounted () {
    this.axios.get(process.env.VUE_APP_APIURL + '/message')
      .then(response => {
        const result = response.data.result
        for (const i of result) {
          const limit = i.time = parseInt((Date.now() - i.time) / 1000)
          if (limit < 60) {
            i.content = ''
          } else if (limit >= 60 && limit < 3600) {
            i.content = Math.floor(limit / 60) + ''
          } else if (limit >= 3600 && limit < 86400) {
            i.content = Math.floor(limit / 3600) + ''
          } else if (limit >= 86400 && limit < 2592000) {
            i.content = Math.floor(limit / 86400) + ''
          } else if (limit >= 2592000 && limit < 31104000) {
            i.content = Math.floor(limit / 2592000) + ''
          } else {
            i.content = ''
          }
        }
        this.items = result.slice().reverse()
      })
      .catch(error => {
        this.$vs.notify({
          title: error.response.data.message,
          color: 'danger',
          icon: 'close'
        })
      })
  }
}
</script>
